@import "./grid.scss";

body {
  background: #e4e4e4;
  font-family: "Open Sans";

  h1 {
    color: #4a4a4a;
    font-size: 1.3rem;
    margin: 5px auto;
  }
  h3 {
    font-size: 1rem;
    color: #292929;
    margin: 5px auto;
  }
  h4 {
    font-size: 1rem;
    color: black;
    margin: 3px auto;
  }
}

.logo {
  text-decoration: none;
  width: 6rem;
  margin-left: 10px;
}

.container {
  &__fluid {
    margin: 0 10px;
  }
  &__row {
    display: flex;
    justify-content: flex-start;
  }
}

.movie-card {
  margin: 10px 15px;
  background: white;
  padding: 15px 15px;
  border-radius: 5px;
  display: inline-block;
  width: 300px;
  height: 430px;
  text-align: center;

  &__subtitle {
    display: inline-block;
    margin-top: 5px;
    margin-right: 5px;
    background: #4a4a4a;
    color: white;
    padding: 3px 3px;
    border-radius: 3px;
  }
  &:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  }

  &__container {
    padding: 5px 0;
  }

  &__link {
    text-decoration: none !important;
  }
}

.c-card * {
  display: block;
  box-sizing: border-box;
}

.c-card {
  display: inline-block;
  width: 100%;
  max-width: 350px;
  margin: 20px;

  text-align: left;
  background: #fff;
  box-shadow: 0 6px 10px -5px rgba(0, 0, 0, 0.2);

  &__image {
    width: 340px;
    height: 500px;
  }

  &__body {
    padding: 20px;

    span {
      display: inline-block;
      margin-right: 5px;
      background: #4a4a4a;
      color: white;
      padding: 1px 3px;
    }
  }

  &__title {
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1.2;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
  }
  &__subtitle {
    font-size: 13px;
    margin-top: 5px;
    padding: 0;
  }

  &__intro {
    margin-bottom: 0;
    max-width: 500px;
  }

  &__footer {
    padding: 20px;
    border-top: 1px solid #ddd;
    span {
      display: inline-block;
      margin: 0 5px;
      background: #4a4a4a;
      color: white;
      padding: 2px 4px;
    }
  }
}
.card-container {
  max-width: 900px;
  margin: 0 auto;
}
.c-card--wide {
  max-width: 100%;
}

.c-card {
  &__header {
    float: left;
  }

  &__body {
    float: left;
    padding-top: 30px;
  }
  &__footer {
    clear: both;
    text-align: center;
  }
}

.sidebar {
  margin: 80px 20px;
  box-sizing: border-box;
  text-align: center;
  padding: 1px 1px;
  width: 60%;
  display: inline-block;

  &__title {
    font-size: 1.1rem !important;
    width: 230px;
    color: #fdff00;
    background: #242424;
    font-weight: 400;

    padding: 7px 5px;
    border-radius: 5px;
  }

  &__item {
    cursor: pointer;
    padding: 4px 4px;
    border: 1px solid white;
    border-radius: 5px;
    background: white;
    &:hover {
      box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    }
    &__active {
      background: rgb(78, 78, 78);
      color: white;
      border: 1px solid rgb(78, 78, 78);
    }
  }
}
